@font-face{font-family:roboto}*{box-sizing:border-box;margin:0;color:333333}h1{font-size:25;margin-top:50px;margin-bottom:30px}h2{font-size:15px;font-weight:900;margin-top:34px;margin-bottom:19px}h3{font-size:10px;font-weight:normal;font-style:italic}h4{font-family:inter;font-size:1px;color:666666;font-style:italic;font-weight:normal}h5{font-size:5px;font-weight:900;color:666666}p{font-family:inter;font-size:1px;color:666666;font-style:italic;margin-top:2px}#root{margin:0;padding-top:30px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.cv__bound{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:595px;height:842px;background-color:#fff;box-shadow:rgba(0,0,0,0.5) 10px 10px 20px}.cv__leftbox{position:absolute;width:37px;height:368px;background-color:#f1bb1a;z-index:0}.cv__bottombox{position:absolute;width:595px;height:16px;background-color:#f1bb1a;-webkit-transform:translateY(826px);transform:translateY(826px)}.cv__content{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;z-index:1}.cv__content .cv__left{-webkit-flex:1.4 1;flex:1.4 1}.cv__content .cv__left .cv__info{margin-bottom:50px}.cv__content .cv__left .cv__skill,.cv__content .cv__left .cv__language,.cv__content .cv__left .cv__hobbies{margin-left:30px}.cv__content .cv__left .cv__image{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-top:50px;margin-bottom:34px}.cv__content .cv__left .cv__image>img{width:122px}.cv__content .cv__right{-webkit-flex:2 1;flex:2 1;padding:40px}

.InfoItem__bound{margin-bottom:12px}.InfoItem__content{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.InfoItem__content .InfoItem__left{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:36px}.InfoItem__content .InfoItem__left>img{width:12px}.InfoItem__content .InfoItem__right{-webkit-flex:1 1;flex:1 1;padding-left:10px}

